import { Component, NgZone } from '@angular/core';
import { NavController, ToastController, AlertController, ActionSheetController, ItemSliding } from 'ionic-angular';
import { CrestronSetupProvider } from '../../providers/crestron-setup-provider';
import { demoType } from '../../providers/crestron-setup-provider';

import { SettingsPage } from '../settings/settings';
import { AddPage } from '../add/add';

import { ICaControlSystemEntry } from '../../providers/crestron-setup-json';
import { ICaResult } from '../../providers/crestron-setup-json';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  devices: Array<ICaControlSystemEntry>;

  editSelect: string;

  public changeDetectorRef;

  constructor(public navCtrl: NavController, public crestronSetupProvider: CrestronSetupProvider, public toastCtrl: ToastController, 
    public alertCtrl: AlertController, public actionSheetCtrl: ActionSheetController, public zone: NgZone) {
    this.crestronSetupProvider.onLaunchDemo = this.onDemoLaunch;
    //this.crestronSetupProvider.onConnectToControlSystem = this.onControlSystemPressed;
    //this.crestronSetupProvider.onlaunchSettingsScreen = this.onSettingsPressewd;

    // use bind for all
    this.crestronSetupProvider.onCaControlSystems = this.onControlSystems.bind(this);
    this.crestronSetupProvider.onConnectToControlSystem = this.onConnectToControlSystem.bind(this);
    this.crestronSetupProvider.onDownloadProject = this.onDownloadProject.bind(this);
    this.crestronSetupProvider.onDeleteControlSystemEntry = this.onDeleteControlSystem.bind(this);
    
    this.editSelect = "-1";
    // comment out if running without backend hookup
    this.crestronSetupProvider.getControlSystems();
  }

  ionViewDidLoad() {

  }


  ionViewDidEnter() {
    this.crestronSetupProvider.getControlSystems();
  }

  // attempt to connect
  selectSystem(event, index) {
    this.crestronSetupProvider.connectToControlSystem(this.devices[index]);
    console.log(this.devices[index].friendlyName + " selected for connection");
  }

  onConnectToControlSystem(result: ICaResult) {
   
  }

  onDownloadProject(result: ICaResult) {
    if (!result.successFlag) {
      let alert = this.alertCtrl.create({
        title: 'Error',
        subTitle: 'Could not download project.',
        buttons: ['OK']
      });
      alert.present();
    }
  }

  addPressed(event) {
    console.log("Add button pressed");
    this.editSelect = "-1";
    var device: ICaControlSystemEntry;
      device = {
      controlSystemID: "-1",
      friendlyName: "",
      useLocalProject: false,
      hostName1: "",
      httpPort1: 80,
      httpPort2: 80,
      ipid: 80,
      cipPort1: 41794,
      cipPort2: 41794,
      address2Enabled: false,
      hostName2: "",
      useSSL: false,
      useNotificationWidget: false,
      userName: "",
      userPassword: ""
    }
    this.navCtrl.push(AddPage, {
      'editSelect': device
    });
  }

  editSystem(event, index, slidingItem) {
    console.log('Edit system pressed for system:' + this.devices[index].friendlyName);
    slidingItem.close();
    this.editSelect = this.devices[index].controlSystemID;
    this.navCtrl.push(AddPage, {
      'editSelect': this.devices[index]
    });
  }

  deleteSystem(event, index, slidingItem) {
    slidingItem.close();
    let alert = this.alertCtrl.create({
      title: 'Confirm Delete',
      message: 'Are you sure you want to delete ' + this.devices[index].friendlyName + '?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Delete',
          role: 'destructive',
          handler: () => {
            console.log('Delete selected for: ' + this.devices[index].friendlyName);
            this.crestronSetupProvider.deleteControlSystemEntry(this.devices[index]);
          }
        }
      ]
    });
    alert.present();
  }

  onDeleteControlSystem(caResult: ICaResult) {
    console.log('delete system complete');
    this.crestronSetupProvider.getControlSystems();
  }

  settingsPressed(event) {
    console.log("Settings button pressed");
    this.crestronSetupProvider.launchSettingsScreen();
    this.navCtrl.push(SettingsPage);
  }

  onSettingsPressewd(caResult) {
    console.log('Settings Pressed');
  }

  launchDemo() {
    //var p = demoType.Phone;
    //var t = demoType.Tablet;
    this.crestronSetupProvider.launchDemo(demoType.Phone);
  }

  onDemoLaunch(caResult) {
    let toast = this.toastCtrl.create({
      message: "Demo back from timeout.",
      duration: 2000
    });
    toast.present();
  }

  controlSystemPressed(device) {
    this.crestronSetupProvider.connectToControlSystem(device);
  }
  
  onControlSystems(caControlSystems: Array<ICaControlSystemEntry>) {
    console.log ("onControlSystems: "+caControlSystems);
    this.zone.run(() => {
      this.devices = caControlSystems;
    });
  }

  openAppStore() {
    //this.market.open('');
  }


}



