import { Component } from '@angular/core';
import { NavController, ToastController, AlertController } from 'ionic-angular';
import { CrestronSetupProvider } from '../../providers/crestron-setup-provider';

import { HomePage } from '../home/home';
import { VoipPage } from '../voip/voip';
import { PrivacyPage } from '../privacy-page/privacy-page';
import { TouPage } from '../tou-page/tou-page';

@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html'
})
export class SettingsPage {

  settings: Array<{ title: string, url: string, settingType: number }>;
  readonly typeURL = 1;
  readonly typePDF = 2;
  readonly typeExportLog = 3;
  readonly typeVersion = 4;
  readonly other = 5;

  keyClicksEnabled: boolean;
  appVersionString: string;
  screeWidth: number;
  screenHeight: number;
  wifiAddress: string;
  localProjectName: string;
  localProjectCompileDate: string;

  constructor(public navCtrl: NavController, public crestronSetupProvider: CrestronSetupProvider, public toastCtrl: ToastController,
    public alertCtrl: AlertController ) {
    this.crestronSetupProvider.onExportSettings = this.onExportSettings;
    this.crestronSetupProvider.onExportLogs = this.onExportLogs;
    this.crestronSetupProvider.onEnableKeyclicks = this.onKeyClicksEnable;

    // temporary test values
    this.keyClicksEnabled = true;
    this.wifiAddress = "172.30.242.175";
    this.localProjectName = "ProjectName";
    this.localProjectCompileDate = "N/A";
    this.appVersionString = "X.X";

    //TODO grab JSON object with 
    this.crestronSetupProvider.launchSettingsScreen();

    this.settings = [
      //{ title: 'About', url: "", settingType: this.typeVersion },
      { title: 'Mobile Apps Terms of Use', url: "http://www.crestron.com/legal-mobile-apps-tou", settingType: this.other },
      { title: 'Privacy Statement', url: "http://www.crestron.com/legal-data-collection-privacy", settingType: this.other }
    ];
  }

  backPressed(event) {

  }

  voipPressed() {
    this.navCtrl.push(VoipPage);
  }

  exportSettings() {
    let toast = this.toastCtrl.create({
      message: "Start Export Settings",
      duration: 2000
    });
    toast.present();
    this.crestronSetupProvider.exportSettings();
  }

  onExportSettings(caResult) {
    let toast = this.toastCtrl.create({
      message: "Finish Export Settings",
      duration: 5000
    });
    toast.present();
  }

  exportLogs() {
    let toast = this.toastCtrl.create({
      message: "Start Export Logs",
      duration: 2000
    });
    toast.present();
    this.crestronSetupProvider.exportLogs();
  }
  
  onExportLogs(caResult) {
    let toast = this.toastCtrl.create({
      message: "Finish Export Logs",
      duration: 2000
    });
    toast.present();
  }

  keyClicksEnable() {
    console.log("Key clicks enabled: " + this.keyClicksEnabled);
    this.crestronSetupProvider.enableKeyclicks();
  }
  
  onKeyClicksEnable(caResult) {

  }

  openSetting(setting) {
    let toast = this.toastCtrl.create({
      message: "Not yet implemented",
      duration: 2000
    });
    toast.present();
    //TODO implement
  }

  aboutSelected() {
      let alert = this.alertCtrl.create({
        title: 'About Crestron App',
        subTitle: 'Version ' + this.appVersionString + '<br\> @2017 Crestron Electronics, Inc',
        buttons: ['OK']
      });
      alert.present();
  }
  privacySelected() {
    this.navCtrl.push(PrivacyPage);
  }

  touSelected() {
    this.navCtrl.push(TouPage);
  }

}



