import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { CrestronSetupProvider } from '../providers/crestron-setup-provider';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { SettingsPage } from '../pages/settings/settings';
import { AddPage } from '../pages/add/add';
import { VoipPage } from '../pages/voip/voip';
import { SaveAndTestPage } from '../pages/save-and-test/save-and-test';
import { EnterNumberPage } from '../pages/enter-number/enter-number';
import { KeyboardUtility } from '../providers/keyboard-utility';
import { PrivacyPage } from '../pages/privacy-page/privacy-page';
import { TouPage } from '../pages/tou-page/tou-page';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    SettingsPage,
    AddPage,
    VoipPage,
    SaveAndTestPage,
    PrivacyPage,
    TouPage,
    EnterNumberPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    SettingsPage,
    AddPage,
    VoipPage,
    SaveAndTestPage,
    PrivacyPage,
    TouPage,
    EnterNumberPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    CrestronSetupProvider,
    KeyboardUtility,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



