import { AfterViewInit, ViewChild, Component, NgZone } from '@angular/core';
import { Platform, ViewController, NavParams, ToastController, Events } from 'ionic-angular';
import { CrestronSetupProvider } from '../../providers/crestron-setup-provider';

@Component({
    templateUrl: 'enter-number.html'
})
export class EnterNumberPage implements AfterViewInit {
    @ViewChild('numInput') numInput;

    ngAfterViewInit() {
        if (this.numInput) {
            this.numInput.setFocus();
        }
    }

    iOS: boolean;
    action: string;
    newNumber: string;
    constructor(public viewCtrl: ViewController, public platform: Platform, public events: Events, 
        public zone: NgZone, public toastCtrl: ToastController, public navParams: NavParams, public crestronSetupProvider: CrestronSetupProvider) {
        this.iOS = this.platform.is('ios');
        this.action = this.navParams.data.action;
        
    }

    ionViewDidLoad() {
        
    }

    onChanged(event) {
        var num: string = event;
        if (num.length >= 4) {
            this.viewCtrl.dismiss(num);
        }
    }

    cancelButtonPressed(event) {
        this.viewCtrl.dismiss();
    }


}


