import { Component, NgZone, ViewChild } from '@angular/core';
import { NavController, ModalController } from 'ionic-angular';
import { CrestronSetupProvider } from '../../providers/crestron-setup-provider';
import { EnterNumberPage } from '../enter-number/enter-number';
import { KeyboardUtility } from '../../providers/keyboard-utility'

import { SaveAndTestPage } from '../save-and-test/save-and-test';
import { ICaVOIPSettings } from '../../providers/crestron-setup-json';

@Component({
  selector: 'page-voip',
  templateUrl: 'voip.html'
})
export class VoipPage {

    @ViewChild('sipExtInput') sipExtInput;
    editSipExtFlag: boolean = false;
    @ViewChild('localInput') localInput;
    editLocalFlag: boolean = false;
    @ViewChild('nameInput') nameInput;
    editNameFlag: boolean = false;
    @ViewChild('hostInput') hostInput;
    editHostFlag: boolean = false;
    @ViewChild('portInput') portInput;
    editPortFlag: boolean = false;
    @ViewChild('usernameInput') usernameInput;
    editUsernameFlag: boolean = false;
    @ViewChild('passwordInput') passwordInput;
    editPasswordFlag: boolean = false;
    @ViewChild('pagingInput') pagingInput;
    editPagingFlag: boolean = false;

    sipExtension: string;
    localPort: string;
    displayName: string;
    sipServerHostName: string;
    sipServerPort: string;
    sipServerUsername: string;
    sipServerPassword: string;
    sipServerPasswordPlaceHolder: string;
    ravaPagingGroups: string;

    enterTestMode: boolean;

    numModalCtrl: any;

  constructor(public navCtrl: NavController , public crestronSetupProvider: CrestronSetupProvider, public modalCtrl: ModalController, public zone: NgZone,
   public keyboardUtil: KeyboardUtility) {
    this.crestronSetupProvider.onSaveVOIPSettings = this.onSavePressed;

    //TODO grab JSON object with 
    this.crestronSetupProvider.editVOIPSettings();

    //TODO remove temp values
    this.sipExtension = "12345"
    this.localPort = "4060";
    this.sipServerPort = "5060";
    this.displayName = "Test Name";
    this.sipServerHostName = "Server Name 1";
    this.sipServerUsername = "user";
    this.sipServerPassword = "crestron";
    this.sipServerPasswordPlaceHolder = "*******"
  }

  changeNumber(event) {
    
  }

  //TODO turn off these fancy input switches
  editSipExt() {
    this.zone.run(() => {
      this.editSipExtFlag = true;
      setTimeout(() => {
        this.sipExtInput.setFocus();
      },150);
    });
  }
  onSipExtChanged(event) {
    this.sipExtension = event;
    this.editSipExtFlag = false;
  }

  editLocal() {
    this.zone.run(() => {
      this.editLocalFlag = true;
      setTimeout(() => {
        this.localInput.setFocus();
      },150);
    });
  }
  onLocalPortChanged(event) {
    this.localPort = event;
    this.editLocalFlag = false;
  }

  editName() {
    this.zone.run(() => {
      this.editNameFlag = true;
      setTimeout(() => {
        this.nameInput.setFocus();
      },150);
    });
  }
  onDisplayNameChanged(event) {
    this.displayName = event;
    this.editNameFlag = false;
  }

  editHost() {
    this.zone.run(() => {
      this.editHostFlag = true;
      setTimeout(() => {
        this.hostInput.setFocus();
      },150);
    });
  }
  onSIPServerOrIPChanged(event) {
    this.sipServerHostName = event;
    this.editHostFlag = false;
  }

  editPort() {
    this.zone.run(() => {
      this.editPortFlag = true;
      setTimeout(() => {
        this.portInput.setFocus();
      },150);
    });
  }
  onServerPortChanged(event) {
    this.sipServerPort = event;
    this.editPortFlag = false;
  }

  editUsername() {
    this.zone.run(() => {
      this.editUsernameFlag = true;
      setTimeout(() => {
        this.usernameInput.setFocus();
      },150);
    });
  }
  onUsernameChanged(event) {
    this.sipServerUsername = event;
    this.editUsernameFlag = false;
  }

  editPassword() {
    this.zone.run(() => {
      this.editPasswordFlag = true;
      setTimeout(() => {
        this.passwordInput.setFocus();
      },150);
    });
  }
  onPasswordChanged(event) {
    this.sipServerPassword = event;
    this.sipServerPasswordPlaceHolder = "";
    for (var i = 0; i < this.sipServerPassword.length; i ++) {
      this.sipServerPasswordPlaceHolder += "*";
    }
    this.editPasswordFlag = false;
  }

  editPaging() {
    this.zone.run(() => {
      this.editPagingFlag = true;
      setTimeout(() => {
        this.pagingInput.setFocus();
      },150);
    });
  }
  onPagingGroupsChanged(event) {
    this.ravaPagingGroups = event;
    this.editPagingFlag = false;
  }

  savePressed(event) {
    // TODO figure out how to make Voip object parameter is expecting
    var obj: ICaVOIPSettings;
    obj['caVoipSettings'].push({ "sipExtension":this.sipExtension,
      "localPort":this.localPort,
      "displayName":this.displayName,
      "ipsipServerHostNameid":this.sipServerHostName,
      "sipServerPort":this.sipServerPort,
      "sipServerUsername":this.sipServerUsername,
      "sipServerPassword":this.sipServerPassword,
      "ravaPagingGroups":this.ravaPagingGroups
    });
    this.crestronSetupProvider.saveVOIPSettings(obj);
    this.navCtrl.pop();
  }

  onSavePressed(caResult) {
    this.navCtrl.pop();
  }

  saveAndTest() {

    this.enterTestMode = true;
    var obj: ICaVOIPSettings;
    obj['caVoipSettings'].push({ "sipExtension":this.sipExtension,
      "localPort":this.localPort,
      "displayName":this.displayName,
      "ipsipServerHostNameid":this.sipServerHostName,
      "sipServerPort":this.sipServerPort,
      "sipServerUsername":this.sipServerUsername,
      "sipServerPassword":this.sipServerPassword,
      "ravaPagingGroups":this.ravaPagingGroups
    });
    this.crestronSetupProvider.saveVOIPSettings(obj);
    this.navCtrl.push(SaveAndTestPage);
  }

}



