import { Component, NgZone, Input } from '@angular/core';
import { NavController, ViewController, AlertController, NavParams } from 'ionic-angular';
import { KeyboardUtility } from '../../providers/keyboard-utility'

import { CrestronSetupProvider } from '../../providers/crestron-setup-provider';
import { ICaControlSystemEntry } from '../../providers/crestron-setup-json';

@Component({
  selector: 'page-add',
  templateUrl: 'add.html'
})
export class AddPage {

  prompt: string;
  system: Array<ICaControlSystemEntry>;
  newSystem: boolean;
  editOrAdd: string;

  // Each control system ID is unique within a device
  controlSystemID: string;
  friendlyName: string;
  useLocalProject: boolean;

  // Note ipid is in decimal format - should be converted to/from hex for display and editing
  ipid: number;
  hostName1: string;
  httpPort1: number;
  cipPort1: number;

  // If address2Enabled is false, the secondary address fields are ignored
  address2Enabled: boolean;
  hostName2: string;
  httpPort2: number;
  cipPort2: number;

  //TODO save booleans correctly, not persisting
  useSSL: boolean;
  useNotificationWidget: boolean

  userName: string;
  userPassword: string;
  userPasswordConfirm:string;

  entry: ICaControlSystemEntry;
  editSelect: number;

  useLocalProjectFlag: boolean;
  address2EnabledFlag: boolean;
  useSSLFlag: boolean;
  useNotificationWidgetFlag: boolean;

  constructor(public navCtrl: NavController, public crestronSetupProvider: CrestronSetupProvider, public viewCtrl: ViewController,
    public keyboardUtil: KeyboardUtility, public alertCtrl: AlertController, public zone: NgZone, public navParams: NavParams) {

      this.crestronSetupProvider.onSaveControlSystemEntry = this.onSaveControlSystemEntry.bind(this);
      this.crestronSetupProvider.ongetControlSystemEntry = this.ongetControlSystemEntry.bind(this);

      this.entry = this.navParams.get('editSelect');

      console.log("EDIT SELECT: " + this.editSelect);

    if (this.entry.controlSystemID !== "-1") {
      this.newSystem = false;
      this.editOrAdd = "Edit";
      this.prompt = "";
      console.log("Edit control system entered");
      //this.crestronSetupProvider.getControlSystemEntry(this.editSelect);
    } else {
      this.newSystem = true;
      this.editOrAdd = "Add";
      this.prompt = "Enter values for new system below.";
    }
    this.controlSystemID = this.entry.controlSystemID;
    this.friendlyName = this.entry.friendlyName;
    this.useLocalProjectFlag = this.entry.useLocalProject;
    this.hostName1 = this.entry.hostName1;
    this.httpPort1 = this.entry.httpPort1;
    this.httpPort2 = this.entry.httpPort2;
    this.ipid  = this.entry.ipid;
    this.cipPort1 = this.entry.cipPort1;
    this.cipPort2 = this.entry.cipPort2;
    this.address2EnabledFlag = this.entry.address2Enabled;
    this.hostName2 = this.entry.hostName2;
    this.useSSLFlag = this.entry.useSSL;
    this.useNotificationWidgetFlag = this.entry.useNotificationWidget;
    this.userName = this.entry.userName;
    this.userPassword = this.entry.userPassword;
    this.userPasswordConfirm = this.userPassword;
  }

  ongetControlSystemEntry(e: ICaControlSystemEntry) {
    this.zone.run(() => {
      this.entry = e;
      this.controlSystemID = this.entry.controlSystemID;
      this.friendlyName = this.entry.friendlyName;
      this.useLocalProject = this.entry.useLocalProject;
      this.hostName1 = this.entry.hostName1;
      this.httpPort1 = this.entry.httpPort1;
      this.httpPort2 = this.entry.httpPort2;
      this.ipid  = this.entry.ipid;
      this.cipPort1 = this.entry.cipPort1;
      this.cipPort2 = this.entry.cipPort2;
      this.address2Enabled = this.entry.address2Enabled;
      this.hostName2 = this.entry.hostName2;
      this.useSSL = this.entry.useSSL;
      this.useNotificationWidget = this.entry.useNotificationWidget;
      this.userName = this.entry.userName;
      this.userPassword = this.entry.userPassword;
      this.userPasswordConfirm = this.userPassword;
    });
  }

  startInput() {
    this.zone.run(() => {
      this.prompt = "";
    });
  }

  onNameChanged(event) {
    this.zone.run(() => {
      this.entry.friendlyName = event;
    });
    console.log("friendly name change: " + event);
  }

  onHostChanged(event) {
    this.zone.run(() => {
      this.entry.hostName1 = event;
    });
    console.log("host name change: " + event);
  }

  onPortChanged(event) {
    this.zone.run(() => {
      this.entry.httpPort1 = +event;
    });
    console.log("port 1 change: " + event);
  }

  onIdChanged(event) {
    this.zone.run(() => {
      this.entry.ipid = +event;
    });
    console.log("ipid change: " + event);
  }

  onCipChanged(event) {
    this.zone.run(() => {
      this.entry.cipPort1 = +event;
    });
    console.log("cip port 1 change: " + event);
  }

  onHost2Changed(event) {
    this.zone.run(() => {
      this.entry.hostName2 = event;
    });
    console.log("host name 2 change: " + event);
  }

  onCip2Changed(event) {
    this.zone.run(() => {
      this.entry.cipPort2 = +event;
    });
    console.log("cip port 2 change: " + event);
  }

  onPort2Changed(event) {
    this.zone.run(() => {
      this.entry.httpPort2 = +event;
    });
    console.log("port 2 change: " + event);
  }

  //TODO figure out how these will be represented in the back end
  onUsernameChanged(event) {
    this.zone.run(() => {
      this.entry.userName = event;
    });
  }

  onPasswordChanged(event) {
    this.zone.run(() => {
      this.entry.userPassword = event;
    });
  }

  onSSLChanged(event) {
    console.log("onSSLChanged: "+event);
    this.zone.run(() => {
      console.log("this.entry.useSSL: "+this.entry.useSSL);
      this.entry.useSSL = event.value;
    });
  }
  

  onConfirmPasswordChanged(event) {
    this.zone.run(() => {
      this.userPasswordConfirm = event;
    });
  }

  savePressed(event) {
    this.entry.useLocalProject = this.useLocalProjectFlag;
    this.entry.address2Enabled = this.address2EnabledFlag;
    this.entry.useSSL = this.useSSLFlag;
    this.entry.useNotificationWidget = this.useNotificationWidgetFlag;
    if (this.useSSLFlag && (this.entry.userPassword !== this.userPasswordConfirm)) { 
      this.userPassword = "";
      this.userPasswordConfirm = this.userPassword;
      let alert = this.alertCtrl.create({
        title: 'Invalid Password',
        subTitle: 'Your passwords must match.',
        buttons: ['OK']
      });
      alert.present();
    } 
    else if ((this.hostName1.length === 0) || (!this.hostName1.length)) {
      let alert = this.alertCtrl.create({
        title: 'Invalid Entry',
        subTitle: 'You must enter a hostname or IP address',
        buttons: ['OK']
      });
      alert.present();
    } else {
        
        this.crestronSetupProvider.saveControlSystemEntry(this.entry);
      }
  }

  onSaveControlSystemEntry() {
    this.navCtrl.pop();
  }

  ionViewWillLeave() {
    this.editSelect = -1;
    // TODO fix bug in android where keyboard puashes up the home screen permenantly
  }

  keyPressed(event, emailField) {
    this.keyboardUtil.doneEditingInput(event, emailField);
  }

  // popup control (not using)
  enterStuff() {
    let prompt = this.alertCtrl.create({
      title: 'Friendly Name',
      message: "Enter a friendly name for the control system",
      inputs: [
        {
          name: 'val',
          placeholder: 'Name'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Save',
          handler: data => {
            this.zone.run(() => {
              this.friendlyName = data.val;
            });
          }
        }
      ]
    });
    prompt.present();
  }

}



