import { Injectable } from '@angular/core';
import { Events } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { ToastController } from 'ionic-angular';


import * as CrestronSetupJson from "./crestron-setup-json";


// Handle to js lib
declare var SetupBridgeInterfaceLibObj: any;


interface caControlSystemsCallBack { (caControlSystems: Array<CrestronSetupJson.ICaControlSystemEntry>): void };
interface caControlSystemEntryCallBack { (caControlSystemEntry: CrestronSetupJson.ICaControlSystemEntry): void };
interface caMainSettingsCallBack { (caMainSettings: CrestronSetupJson.ICaMainSettings): void };
interface caVOIPSettingsCallBack { (caVOIPSettings: CrestronSetupJson.ICaVOIPSettings): void };
interface caResultCallBack { (caResult: CrestronSetupJson.ICaResult): void };

export enum demoType {
  Phone,
  Tablet,
};

@Injectable()
export class CrestronSetupProvider {


  public onCaControlSystems: caControlSystemsCallBack; // callback to handle caControlSystems JSON object
  public onCaControlSystemsEntry: caControlSystemEntryCallBack; // callback to handle caControlSystemEntry JSON object
  public onSaveControlSystemEntry: caResultCallBack; // callback to handle caResult JSON object after a saveControlSystemEntry call
  public onDeleteControlSystemEntry: caResultCallBack; // callback to handle caControlSystems JSON object after a deleteControlSystemEntry call
  public onConnectToControlSystem: caResultCallBack; // callback to handle caResult JSON object after a connectToControlSystem call
  public onDownloadProject: caResultCallBack; // callback to handle caResult JSON object after a connectToControlSystem call
  public ongetControlSystemEntry: caControlSystemEntryCallBack; // callback to handle caResult JSON object after a connectToControlSystem call

  public onLaunchDemo: caResultCallBack; // callback to handle caResult JSON object after a launchDemo call
  public onPreviewControlSystem: caResultCallBack; // callback to handle caResult JSON object after a previewControlSystem call
  public onlaunchSettingsScreen: caMainSettingsCallBack; // callback to handle caMainSettings JSON object after a launchSettingsScreen call

  public onExportSettings: caResultCallBack; // callback to handle caResult JSON object after an exportSettings call
  public onExportLogs: caResultCallBack; // callback to handle caResult JSON object after an exportLogs call

  public onEnableKeyclicks: caResultCallBack; // callback to handle caResult JSON object after an enableKeyclicks call

  public onEditVOIPSettings: caVOIPSettingsCallBack; // callback to handle caVOIPSettings JSON object after an editVOIPSettings call
  public onSaveVOIPSettings: caResultCallBack; // callback to handle caResult JSON object after a saveVOIPSettings call
  public onVoipDial: caResultCallBack; // callback to handle caResult JSON object after a voipDial call
  public onVoipAnswer: caResultCallBack; // callback to handle caResult JSON object after a voipAnswer call
  public onVoipHangUp: caResultCallBack; // callback to handle caResult JSON object after a voipHangUp call
  public onVoipPageAll: caResultCallBack; // callback to handle caResult JSON object after a voipPageAll call

  //systems: Array<ICaControlSystemEntry>;

  public className: string;

  constructor(public events: Events, public toastCtrl: ToastController) {
    // dont want any values stored here
    this.className = 'CrestronSetupProvider'; 

    this.nativeEventDispatcher.bind(this);
    SetupBridgeInterfaceLibObj.SetNativeEventDispatcher(this.nativeEventDispatcher, this);

  }

  public nativeEventDispatcher(msg) {
    try {
      var context = msg.eventContext;
      var returnSuccess = msg.returnSuccess;
      var returnCode = msg.returnCode;
      var returnObject = msg.returnObject;

      // need context to determine which callback needed

      console.log("nativeEventDispatcher: context: "+context);
      switch (context) {
        case 'getControlSystems':
          if (this.onCaControlSystems) // if onCaControlSystems callback registered
          {
            console.log("returnObject "+returnObject);
            var caControlSystems = returnObject as Array<CrestronSetupJson.ICaControlSystemEntry>;
            console.log("nativeEventDispatcher: context: "+context+ ", calling onCaControlSystems callback");
            this.onCaControlSystems(caControlSystems);
          }
          break;
        case 'deleteControlSystemEntry':
          if (this.onDeleteControlSystemEntry) // if onDeleteControlSystemEntry callback registered
          {
            var caRes = returnObject.caResult as CrestronSetupJson.ICaResult;
            console.log("nativeEventDispatcher: context: "+context+ ", calling onDeleteControlSystemEntry callback");
            this.onDeleteControlSystemEntry(caRes);
          }
          break;
        case 'getControlSystemEntry':
          if (this.ongetControlSystemEntry) // if ongetControlSystemEntry callback registered for edit
          {
            var caControlSystemEntry = returnObject.caControlSystemEntry as CrestronSetupJson.ICaControlSystemEntry;
            console.log("nativeEventDispatcher: context: "+context+ ", calling ongetControlSystemEntry callback");
            this.ongetControlSystemEntry(caControlSystemEntry);
          }
          break; 
        case 'saveControlSystemEntry':
            if (this.onSaveControlSystemEntry) // if onSaveControlSystemEntry callback registered for save
            {
              var caRes = returnObject.caResult as CrestronSetupJson.ICaResult;
              console.log("nativeEventDispatcher: context: "+context+ ", calling onSaveControlSystemEntry callback");
              this.onSaveControlSystemEntry(caRes);
            }
            break;                           
        case 'connectToControlSystem':
          console.log('CrestronSetupProvider connectToControlSystem returned');
          if (this.onConnectToControlSystem) // if onConnectToControlSystem callback registered
          {
            var caRes = returnObject.caResult as CrestronSetupJson.ICaResult;
            console.log("nativeEventDispatcher: context: "+context+ ", calling onConnectToControlSystem callback");
            this.onConnectToControlSystem(caRes);
          }
          break; 

        case 'downloadProject':
          console.log('CrestronSetupProvider connectToControlSystem returned');
          if (this.onDownloadProject) // if onConnectToControlSystem callback registered
          {
            var caRes = msg as CrestronSetupJson.ICaResult;
            console.log("nativeEventDispatcher: context: "+context+ ", calling onConnectToControlSystem callback");
            this.onDownloadProject(caRes);
          }
          break; 
        default:
          console.log("No match for context: " + context);
          break;
      }
    }
    catch (e) { 
          console.log(e);
    }
  }

  public LaunchGenTest() {
    SetupBridgeInterfaceLibObj.LaunchGenTest();
  }

  public getControlSystems() {

    var obj = JSON.parse('{"eventContext": "getControlSystems","returnSuccess": true,"returnCode": 0,"returnObject":[{"address2Enabled":"true","cipPort1":41796,"cipPort2":41796,"controlSystemID":"1","friendlyName":"Colin\u0027s NYC mansion","hostName1":"192.168.42.42","hostName2":"colin_nyc.mycrestron.com","httpPort1":443,"httpPort2":8043,"ipid":6,"useLocalProject":false,"useNotificationWidget":false,"useSSL":true},{"address2Enabled":"true","cipPort1":41796,"cipPort2":41796,"controlSystemID":"2","friendlyName":"Colin\u0027s London mansion","hostName1":"192.168.77.77","hostName2":"colin_london.mycrestron.com","httpPort1":443,"httpPort2":8043,"ipid":240,"useLocalProject":false,"useNotificationWidget":false,"useSSL":true},{"address2Enabled":"true","cipPort1":41796,"cipPort2":41796,"controlSystemID":"3","friendlyName":"Colm\u0027s Dalkey home","hostName1":"192.168.177.77","hostName2":"colm.dublin.mycrestron.com","httpPort1":443,"httpPort2":8043,"ipid":140,"useLocalProject":false,"useNotificationWidget":false,"useSSL":true},{"address2Enabled":"true","cipPort1":41796,"cipPort2":41796,"controlSystemID":"4","friendlyName":"Jack\u0027s Colorado luxury ranch","hostName1":"96.84.15.243","hostName2":"jack.co.mycrestron.com","httpPort1":443,"httpPort2":8043,"ipid":64,"useLocalProject":false,"useNotificationWidget":false,"useSSL":true},{"address2Enabled":"true","cipPort1":41796,"cipPort2":41796,"controlSystemID":"5","friendlyName":"Haiwan tuscany villa","hostName1":"196.148.48.191","hostName2":"luxury.hi.mycrestron.com","httpPort1":443,"httpPort2":8043,"ipid":92,"useLocalProject":false,"useNotificationWidget":false,"useSSL":true}]}');

    console.log('CrestronSetupProvider: getControlSystems executed');
    SetupBridgeInterfaceLibObj.getControlSystems();
  }

  public launchDemo(type: demoType) {
    switch (type) {
      case demoType.Phone:
        SetupBridgeInterfaceLibObj.launchDemo('Phone');
      case demoType.Tablet:
        SetupBridgeInterfaceLibObj.launchDemo('Tablet');
    }
  }

  public getControlSystemEntry(controlSystemID: number) {
    SetupBridgeInterfaceLibObj.getControlSystemEntry(controlSystemID);
  }

  public saveControlSystemEntry(caControlSystemEntry: CrestronSetupJson.ICaControlSystemEntry) {
    SetupBridgeInterfaceLibObj.saveControlSystemEntry(caControlSystemEntry);
  }

  public deleteControlSystemEntry(caControlSystemEntry: CrestronSetupJson.ICaControlSystemEntry) {
    SetupBridgeInterfaceLibObj.deleteControlSystemEntry(caControlSystemEntry);
  }

  public connectToControlSystem(caControlSystemEntry: CrestronSetupJson.ICaControlSystemEntry) {
    console.log("caControlSystemEntry: "+ caControlSystemEntry);
    SetupBridgeInterfaceLibObj.connectToControlSystem(caControlSystemEntry);
  }

  public previewControlSystem(controlSystemID: number) {
    SetupBridgeInterfaceLibObj.previewControlSystem(controlSystemID);
  }

  public launchSettingsScreen() {
    SetupBridgeInterfaceLibObj.launchSettingsScreen();
  }

  public exportSettings() {
    SetupBridgeInterfaceLibObj.exportSettings();
  }

  public exportLogs() {
    SetupBridgeInterfaceLibObj.exportLogs();
  }

  public enableKeyclicks() {
    SetupBridgeInterfaceLibObj.enableKeyclicks();
  }

  public editVOIPSettings() {
    SetupBridgeInterfaceLibObj.editVOIPSettings();
  }

  public saveVOIPSettings(caVOIPSettings: CrestronSetupJson.ICaVOIPSettings) {
    SetupBridgeInterfaceLibObj.saveVOIPSettings(caVOIPSettings);
  }

  public voipDial(SIPAddress: string) {
    SetupBridgeInterfaceLibObj.voipDial(SIPAddress);
  }

  public voipAnswer() {
    SetupBridgeInterfaceLibObj.voipAnswer();
  }

  public voipHangUp() {
    SetupBridgeInterfaceLibObj.voipHangUp();
  }

  public voipPageAll() {
    SetupBridgeInterfaceLibObj.voipPageAll();
  }


}


